import React from 'react';
import styled from 'styled-components';
import { Link } from "gatsby";

import { Container } from '@components/global';
import ExternalLink from '@common/ExternalLink';


import { ReactComponent as RoundLogo } from '@static/roundlogo.svg';


const SOCIAL = [
  {
    text: 'Facebook',
    link: 'https://www.facebook.com/rearia.io/',
  },
  {
    text: 'Twitter',
    link: 'https://twitter.com/rearia_io',
  },
  {
    text: 'Instagram',
    link: 'https://www.instagram.com/rearia.io/',
  },
];

const Footer = () => (

    <FooterWrapper>

      <Container>
        <Grid>
          <FooterLogo>
            <Link to="/">
              <RoundLogo/>
            </Link>
          </FooterLogo>
          <FooterBox>
              <b>About Us</b><br/>
              <Link to="about">About</Link><br/>
              <Link to="media">Media</Link><br/>
          </FooterBox>
          <FooterBox>
              <b>Our Product</b><br/>
              <Link to="hai">Hai</Link><br/>
          </FooterBox>
          <FooterBox>
            <b>Follow us</b><br/>
            <SocialLinks>
            {SOCIAL.map(({ text, link }) => (
              <ExternalLink href={link}>
                {text}
              </ExternalLink>
            ))}
            </SocialLinks>
            <b>Contact</b><br/>
            hello@rearia.io<br/>
            <Copyright>
            © Copyright Rearia Co., Ltd. 2020
            </Copyright>
            </FooterBox>
        </Grid>
        <Grid>
          <FooterBox>
            
          </FooterBox>
        </Grid>
      </Container>
    </FooterWrapper>

);


const FooterWrapper = styled.footer`
  
  background-color: ${props => props.theme.color.grayscale.S1000};
  padding: 65px 0;
  
`;

const Grid = styled.div`
  display: flex;
  text-align: left;
  align-items: top;
  justify-items: left;
  margin-bottom: 5px;
  > div
  {
    margin-right: 45px;  
  }
  div:first-child{
    flex-grow: 1;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
    flex-direction: column;
    > div
    {
    margin-right: 0px;  
    margin-top: 5px;  
    }
    div:first-child{
      margin-bottom: 30px;
    }
  }  

`;

const FooterLogo = styled.div`
  display: flex;
`;

const SocialLinks = styled.div`
  display: flex;
  a {
    color: ${props => props.theme.color.white.regular} !important;
    ${props => props.theme.font_size.small};
    margin-right: 20px;
  }
`;

const Copyright = styled.p`
  ${props => props.theme.font_size.xsmall};
  color: ${props => props.theme.color.grayscale.S500} !important;
`;

const FooterBox = styled.div`
  font-family: ${props => props.theme.font.secondary};
  ${props => props.theme.font_size.small};
  color: ${props => props.theme.color.white.regular};
  margin-bottom: 20px;

  a {
    text-decoration: none;
    color: inherit;
    &:hover{
      text-decoration: underline !important;
      color:${props => props.theme.color.secondary.S400} !important;
    }
  }
`;


