import React from 'react';
import styled from 'styled-components';
import addToMailchimp from 'gatsby-plugin-mailchimp'

export default class EmailForm extends React.Component {
  state = {
      email: null,
  }

  _handleChange = (e) => {
      console.log({
          [`${e.target.name}`]: e.target.value,
      });
      this.setState({
          [`${e.target.name}`]: e.target.value,
      });
  }

  _handleSubmit = (e) => {
      e.preventDefault();

      console.log('submit', this.state);

      addToMailchimp(this.state.email, this.state)
          .then(({ msg, result }) => {
              console.log('msg', `${result}: ${msg}`);

              if (result !== 'success') {
                  throw msg;
              }
              alert(msg);
          })
          .catch((err) => {
              console.log('err', err);
              alert(err);
          });
  }

  render() {
    return (
        <MailChimpForm>
          <form onSubmit={this._handleSubmit}>
            <Grid>
            <input
                type="email"
                onChange={this._handleChange}
                placeholder="Your email"
                name="email"
            />
            </Grid>
            <input type="submit" />
        </form>
      </MailChimpForm>
      );
    }
}

const MailChimpForm = styled.div`
  margin: 30px 0;

  input[type=text], input[type=email]{
    width: 100%;
    padding: 12px 5px;
    box-sizing: border-box;
    border-radius: 0px;
    color: ${props => props.theme.color.secondary.S100}; 
    background: ${props => props.theme.color.supplementary.S900}; 
    border: none;
    border-bottom: ${props => props.theme.color.supplementary.S700} 2px solid;
    ::-webkit-input-placeholder { /* Edge */
      color: ${props => props.theme.color.supplementary.S600};
    }
    :-ms-input-placeholder { /* Internet Explorer */
      color: ${props => props.theme.color.supplementary.S600};
    }
    ::placeholder {
      color: ${props => props.theme.color.supplementary.S600};
    }  
    -webkit-transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    -o-transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: all .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
  } 

  input:focus { 
    outline: none; 
  }
  input[type=text]:focus, input[type=email]:focus{
    border-bottom: ${props => props.theme.color.secondary.S400} 2px solid;
  }


  input[type=submit] {
    display: inline-block;
    font-weight: 400;
    text-align: center;
    vertical-align: middle;
    background: ${props => props.theme.color.supplementary.S900}; 
    color: ${props => props.theme.color.grayscale.S100}; 
    border-radius: 100px;
    border: 2px solid;
    border-color: ${props => props.theme.color.grayscale.S100};
    line-height: 24px;
    padding: 11px 25px;
    margin: 30px 0;
    -webkit-transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    transition: all .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;
    -o-transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: all .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: all .15s ease-in-out,box-shadow .15s ease-in-out,-webkit-box-shadow .15s ease-in-out;

    div {
      display:inline-block;
      margin-right: 5px;
      -webkit-transition: all .15s ease-in-out;
      transition: all .15s ease-in-out;
      -o-transition: all .15s ease-in-out;
    }
    &:hover {
      div {
        margin-right: 15px;
      }
      border-color: ${props => props.theme.color.secondary.S400};
      color: ${props => props.theme.color.secondary.S400};
    }
`

const Grid = styled.div`
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 20px;
  text-align: left;
  align-items: top;
  justify-items: left;
  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    text-align: left;
  }
`
;
