import React from 'react';
import styled from 'styled-components';
import { Section, Container } from '@components/global';
import EmailForm from '@ui/emailform';
//import Call2Act from '@ui/Call2Act';
import { StaticQuery, graphql } from 'gatsby';
//import Img from 'gatsby-image';
import Fade from 'react-reveal/Fade';

const Link2Act = () => (
  <StaticQuery
    query={graphql`
      query {
        Link2Act: file(
          sourceInstanceName: { eq: "photos" }
          name: { eq: "link2act" }
        ) {
          childImageSharp {
            fluid(maxWidth: 1450) {
              ...GatsbyImageSharpFluid
            }
          }
        },
      }
    `}
    render={data => (
      <Section id="subscription" accent="primary">
        <Container>
          <HeaderWrapper accent="inverse">
          <Grid>
            <div>
            <Fade>
              <h2>Stay Tuned!</h2>
              <br/>
              <p>We need your help to complete Hai. Please sign up to our newsletter, and follow our social media to get the latest information on Hai. We'll also share tips and tricks about music and hearing aids. If you know someone who'd appreciate having our product, please let them know of Hai too! We need your help to realize Hai for everyone!</p>
              <EmailForm accent="inverse"/>
              {/*
                <Call2Act text="Sign up to our newsletter" href="https://www.indiegogo.com" accent="inverse" target="_blank"/>
              */}
            </Fade>
            </div>
            {/*<Fade>
              <LinkMainImg>
                <Img fluid={data.Link2Act.childImageSharp.fluid} />
              </LinkMainImg>
            </Fade>
  const LinkMainImg = styled.div`
  margin: 0;
  background: #feebe1;
  max-width: 1380px;
  min-width: 35vw;
  padding: auto;
  div{
    margin: 0;
    width: calc(80% - 32px);
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    //min-width: calc(80vw - 32px);
    display: none;
  }
`;*/}
          </Grid>
          </HeaderWrapper>
        </Container>
      </Section>
    )}
  />
);

export default Link2Act;


const HeaderWrapper = styled.div`
  padding: 65px 0 80px 0;
  margin-left: 0%;
  max-width: 100%;
  font-weight: 400;
  vertical-align: middle;
  color: ${props => props.theme.color.grayscale.S1000};
  @media (max-width: ${props => props.theme.screen.md}) {
    max-width: 100%;
    padding: 15px 0 0 0;
    margin-left: 0%;
    margin-bottom: -50px;
  }

  ${props =>
    props.accent &&
    `color: ${
      props.accent === 'inverse'
        ? props.theme.color.white.regular
        : props.theme.color.grayscale.S1000
    }`};

  p {
    ${props =>
      props.accent &&
      `color: ${
        props.accent === 'inverse'
          ? props.theme.color.grayscale.S100
          : props.theme.color.grayscale.S1000
      }`};
  }
`;

const Grid = styled.div`
  display: grid;
  grid-template-columns: 4fr 3fr;
  grid-gap: 50px;
  text-align: left;
  align-items: top;
  justify-items: left;

  @media (max-width: ${props => props.theme.screen.md}) {
    grid-template-columns: 1fr;
    margin-bottom: 96px;
    grid-gap: 25px;

    &:last-child {
      margin-bottom: 24px;
    }
  }
`;




